@import 'variables';
@import 'mixins';

.search-section {
    transition: 0.5s;
    display: flex;
    height: 300px;
    justify-content: center;
    align-items: flex-end;
    padding-bottom: 10px;
    .search-bar {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        .search-bar-container {
            position: relative;
    
        }

        
        .buttons {
            display: flex;
            
            #mainSubmitBtn, button {
                color: $color5;
                width: 100px;
                height: 20px;
                transition: 0.1s;
                margin: 0 10px 0 10px;
                border: 2px solid $color5;
                background: white;
                border-radius: 26px;
                &:hover {
                    cursor: pointer;
                    background: white;
                    transform: scale(1.1);
                }
            }
        }
    }
}

.moved-up {
    
    &.search-section {
        height: 150px;
        #mainSearchBar {
            width: 400px;
            height: 45px;
        }
        .dropdown {
            width: 394px;
        }
    }

}

.autocomplete {
    position: absolute;
    width: 300;
    max-height: 300px;
    overflow: scroll;
    background: white;
    color: $secondary-color;
    border: 3px solid $color5;
    top: 40px;
    left: 15px;
    z-index: 3;
    ul {
        list-style: none;
        li {
            color: black;
        }
    }
}