@import 'variables';
@import 'mixins';

.pokemon-main-info {
    image-rendering: pixelated;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
    height: 200px;
    margin-bottom: $detailed-view-margins;

    .name {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        font-size: 20px;
        letter-spacing: 4px;
        font-weight: 600;
        .genus {
            //display: block;
            letter-spacing: auto; 
            margin-top: 3px;
            font-size: 12px;
        }
    }

    .arrows {
        display: flex;
        justify-content: space-between;
    }

    .type-and-id {
        display: flex;
        justify-content: space-between;
        .type-list {
            display: flex;
            flex-direction: column;
            .type-item {
                display: flex;
                align-items: center;
                .type-icon {
                    image-rendering: auto;
                    width: 16px;
                }
            }
        }
        .pokemon-id {
            align-self: flex-end;
        }
    }
    .background-opacity {
        background: rgba(0, 0, 0, 0.562);
    }
}